@import url("https://fonts.googleapis.com/css?family=Poor+Story");
* {
  font-size: 18px;
  box-sizing: border-box;
  padding: 0;
  margin: 0; }

.navbar {
  font-size: 1rem;
  background-image: linear-gradient(260deg, #2376ae 0%, #c16ecf 100%);
  border: 1px solid rgba(0, 0, 0, 0.2);
  padding: 10px; }

.navbar-toggle {
  position: absolute;
  top: 10px;
  right: 20px;
  cursor: pointer;
  color: rgba(255, 255, 255, 0.8);
  font-size: 1.5rem; }

.main-nav {
  list-style: none;
  display: none; }

.nav-links,
.logo {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.7); }

.main-nav > li {
  text-align: center;
  margin: 15px auto; }

.logo {
  display: inline-block;
  font-size: 22px;
  margin-top: 10px;
  margin-left: 20px; }

.active {
  display: block; }

@media screen and (min-width: 768px) {
  .navbar {
    display: flex;
    justify-content: space-between;
    padding-bottom: 0;
    height: 70px;
    align-items: center; }

  .main-nav {
    display: flex;
    margin-right: 30px;
    flex-direction: row;
    justify-content: flex-end; }

  .main-nav li {
    margin: 0; }

  .nav-links {
    margin-left: 40px; }

  .logo {
    margin-top: 0; }

  .navbar-toggle {
    display: none; }

  .logo:hover,
  .nav-links:hover {
    color: rgba 255, 255, 255, 1; } }
/* The sidebar menu */
.sidenav {
  height: 100vh;
  /* Full-height: remove this if you want "auto" height */
  width: 60px;
  /* Set the width of the sidebar */
  position: fixed;
  /* Fixed Sidebar (stay in place on scroll) */
  z-index: 1;
  /* Stay on top */
  top: 0;
  /* Stay at the top */
  left: 0;
  background-color: rgba(0, 0, 0, 0.85);
  /* Black */
  overflow-x: hidden;
  overflow-y: hidden;
  /* Disable horizontal scroll */
  padding-top: 20px;
  text-align: center; }

.sidenav > a {
  text-decoration: none; }

/* The navigation menu links */
.sidenav-links {
  margin-top: 32vh; }

.sidenav-links a {
  padding: 6px 16px 6px 16px;
  text-decoration: none;
  font-size: 30px;
  color: rgba(255, 255, 255, 0.6);
  display: block; }

/* When you mouse over the navigation links, change their color */
.sidenav-links a:hover {
  color: #f1f1f1; }

/* Style Nav Logo */
.nav-logo {
  font-size: 30px;
  color: #fff;
  background-color: #000;
  padding: 20px; }

.bottom {
  margin-top: 150%;
}

.bottom > a {
  text-decoration: none;
  color:#fff;
}
.bottom > a > i {
  font-size: 32px;
}
