/* Some Fonts */
/* Some Common Variables */
body {
  font-family: 'Josefin Sans', sans-serif;
  background: rgba(0,0,0,0.9);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

/* Style page content */
.main {
  min-height: 100vh;
  width: auto;
  margin-left: 60px;
  /* Same as the width of the sidebar */
  padding: 0px 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  overflow-x: hidden;}

/* introduction */
.introduction p {
  font-family: "Poor Story", cursive;
  color: #fff;
  font-size: 55px; }

.introduction > .inner {
  color: rgba(255, 255, 255, 0.5);
  font-size: 24px;
}

.social-links {
  width: 100%;
  text-align: center; }
  .social-links a {
    text-decoration: none;
    color: rgba(255, 255, 255, 0.7); }


/* Projects */
.project-label{
  color: #fff;
  font-family: "Poor Story", cursive;
  font-size: 24px;
}

.row {
    margin: 8px -16px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

/* Add padding BETWEEN each column */
.row,
.row > .column {
    padding: 8px;
}

/* Create four equal columns that floats next to each other */
.column {
    float: left;
    width: 24%;
}

/* Clear floats after rows */
.row:after {
    content: "";
    display: table;
    clear: both;
}

/* Content */
.content {
    padding: 10px;
    font-family: "Poor Story", cursive;
    color: #fff;
    text-align: center;
}

.content img {
  width: 100%;
}

.content p i {
  margin: 5px;
}

/* Responsive layout - makes a two column-layout instead of four columns */
@media screen and (max-width: 900px) {
    .column {
        width: 50%;
    }
}

/* Responsive layout - makes the two columns stack on top of each other instead of next to each other */
@media screen and (max-width: 600px) {
    .column {
        width: 100%;
    }
}

/* About Section */
.main-about {
  min-height: 100vh;
  width: auto;
  margin-left: 60px;
  /* Same as the width of the sidebar */
  padding: 0px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;}

.about {
  display: grid;
  grid-column-gap: 150px;
  grid-template-columns: auto auto;
  padding: 30px;
  font-family: "Poor Story", cursive;
  color: #fff;
  height: 100%;
}
.about-heading {
  font-size: 32px;
  padding: 20px;
}
.about-text{
  text-align: center;
  font-size: 24px;
}
/* contact section */
.contact {
  font-family: "Poor Story", cursive;
}
.contact p {
  margin: 20px;
}
.contact .email {
  color:#fff;
  font-size: 34px;
  color: rgba(255,255,255, 0.8);
}
.contact .email a {
  text-decoration: none;
    font-size: 24px;
  color: rgba(255,255,255, 0.5);
}
.social-media {
  color:#fff;
  font-size: 34px;
  color: rgba(255,255,255, 0.8);
}
.social-icons a {
  text-decoration: none;
  color: #fff;
}
.contact-label {
  color: #fff;
  font-size: 44px;
}
.social-icons i {
  margin: 0 12px;
}

